@import '../../../../../../../../styles/variables';

.search-for-help-content {
  color: $wk-primary-gray-shade2;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;

  & &_text-bold {
    color: $wk-primary-gray-shade2;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
  }

  & &_list {
    list-style-type: disc;
    margin-left: 24px;
  }

  & &_list-item {
    margin-top: 4px;
  }

  & &_text-green {
    color: $wk-primary-green-shade1;
  }

  & &_margin-bottom-29 {
    margin-bottom: 29px;
  }

  & &_margin-bottom-14 {
    margin-bottom: 14px;
  }

  & &_margin-bottom-10 {
    margin-bottom: 10px;
  }
}
